.waiting-main{
    width: 650px;
    .waiting-header{
        display: flex;
        position: fixed;
        top: 60px;
        background-color: white;
        z-index: 10;
        padding: 10px 0;
        .header-link{
            display: flex;
            line-height: 35px;
            background-color: darkgray;
            border-radius: 35px;
            align-items: center;
            padding: 0 10px;
            margin-left: 10px;
            p{
                align-items: center;
            }
        }
    }
    .waiting-body{
        position: relative;
        padding: 15px;
        top: 40px;
        .question-unit{
            padding: 15px 0;
            border-bottom: 1px solid rgb(235, 235, 235);
            .question-unit-header{
                img{
                    width: 20px;
                    height: 20px;
                    border-radius: 50%;
                    margin-right: 5px;
                }
                span{
                    margin-left: 5px;
                    color: gray;
                }
            }
            .question-unit-body{
                display: flex;
                justify-content: space-between;
                line-height: 30px;
                .unit-body-left{
                    font-size: 15px;
                }
            }
            .question-unit-footer{
                color: gray;
            }
        }
    }
}